.question-bank-container {
  z-index: 90;
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 28px 12px 88px 12px;

  @media (min-width: 800px) {
    padding: 28px 20px 88px 20px;
  }

  h1 {
    color: var(--text-color);
    margin-bottom: 24px;
    text-align: center;
  }

  .questions-list {
    display: flex;
    flex-direction: column;
    gap: 12px;

    .question-item {
      background: var(--background-color);
      padding: 18px;
      border: 1px solid var(--gray-color-13);

      .question-header {
        display: flex;
        gap: 12px;
        margin-bottom: 15px;

        .question-number {
          color: var(--primary-color);
          font-weight: bold;
          font-size: 1.1rem;
        }

        .question-text {
          color: var(--text-color);
          font-size: 1.1rem;
        }
      }

      .options {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin: 15px 0;

        .option {
          padding: 15px;
          background: var(--gray-color-13);
          color: var(--text-color);

          &.correct-answer {
            background: var(--correct-color);
            color: white;
          }
        }
      }

      .answer-info {
        margin-top: 10px;
        color: var(--text-color);

        .answer-label {
          font-weight: bold;
        }

        .answer {
          color: var(--correct-color);
          font-weight: bold;
          margin-left: 8px;
        }
      }
    }
  }
}
