.results-container {
  z-index: 90;
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 48px 12px 12px 12px;

  @media (min-width: 800px) {
    padding: 48px 20px 20px 20px;
  }

  .summary {
    // border: 2px solid var(--gray-color-13);
    padding: 12px;
    margin: 20px 0;

    h1 {
      font-size: 4rem;
      margin: 12px 0;
      color: var(--primary-color);
      font-style: italic;

      span {
        font-size: 1rem;
        font-weight: normal;
        color: var(--gray-color-10);
      }
    }

    p {
      font-size: 1rem;
      color: var(--gray-color-10);
    }
  }

  .filters {
    padding: 12px;
    margin: 20px 0;

    label {
      display: flex;
      align-items: center;
      gap: 8px;
      color: var(--text-color);
      cursor: pointer;
    }
  }

  .questions-review {
    .question-item {
      margin: 15px 0;
      padding: 20px;
      border: 1px solid var(--secondary-color);
      background: var(--background-color);
      transition: all 0.3s ease;

      &.correct {
        border: none;
        border-left: 5px solid var(--correct-color);
        background-color: var(--correct-background-color);
      }

      &.wrong {
        border: none;
        border-left: 5px solid var(--warning-color);
        background-color: var(--wrong-background-color);
      }

      .options {
        margin: 15px 0;

        .option {
          padding: 12px;
          margin: 8px 0;
          background: var(--background-color);

          &.user-answer {
            background: var(--gray-color-12);
            color: white;
          }

          &.correct-answer {
            background: var(--correct-color);
            color: white;
          }
        }
      }

      .answer-status {
        margin-top: 10px;
        font-weight: bold;
        color: var(--text-color);
      }
    }
  }

  .button-section {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 80px;
    padding: 24px 12px;
  }
}
