.quiz-container {
  z-index: 90;
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 28px 12px 88px 12px;

  @media (min-width: 800px) {
    padding: 28px 20px 88px 20px;
  }

  .question-section {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: var(--background-color);
    height: 100%;
    padding: 24px 0;
    border-radius: 5px;
    margin-bottom: 20px;

    @media (min-width: 800px) {
      padding: 24px;
    }

    h2 {
      color: var(--text-color);
      margin-bottom: 15px;
      padding: 0 12px;
    }

    .question-block {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;

      p {
        color: var(--text-color);
        font-size: 1.1rem;
        margin-bottom: 20px;
        padding: 0 12px;
      }

      .options {
        display: flex;
        flex-direction: column;
        gap: 10px;

        button {
          display: flex;
          align-items: center;
          justify-content: start;
          text-align: left;
          padding: 15px;
          background: var(--gray-color-13);
          color: var(--text-color);
          border: 2px solid var(--gray-color-13);
          cursor: pointer;
          gap: 9px;

          // &:hover {
          //   background: var(--gray-color-12);
          // }

          > span {
            display: inline-block;
            font-weight: bold;
          }

          &:hover {
            border: 2px solid var(--gray-color-12);
          }

          &.selected {
            border: 2px solid var(--primary-color);
            background: var(--primary-color);
            color: white;
          }
        }
      }
    }
  }

  .button-section {
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 50%;
    gap: 10px;
    height: 80px;
    width: 100%;
    max-width: 800px;
    padding: 0 12px 24px 12px;
    background: var(--background-color);

    > button {
      flex: 1;
      font-size: 1rem;

      &.finish-button {
        background: var(--primary-color);
        border: 2px solid var(--primary-color);
        color: white;
      }
    }
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
  }
}
