.App {
  text-align: center;
}

.app {
  text-align: center;
  min-height: 100vh;
  background-color: var(--background-color);
  color: var(--text-color);

  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;

    h1 {
      color: var(--text-color);
      margin-bottom: 30px;
    }

    button {
      padding: 12px 24px;
      background: var(--primary-color);
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
      font-size: 1rem;
      transition: all 0.2s ease;

      &:hover {
        background: darken(#2196f3, 10%);
      }
    }
  }
}
