.home-container {
  z-index: 90;
  position: relative;
  width: 100%;
  max-width: 800px;
  height: 100vh;
  margin: 0 auto;
  padding: 24px 0;
  overflow: hidden;
  overflow-y: scroll;
  -ms-overflow-style: none; /* IE å’Œ Edge */
  scrollbar-width: none; /* Firefox */

  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-width: 800px) {
    // padding: 28px 20px 88px 20px;
  }

  .home-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    h1 {
      color: var(--text-color);
      margin-bottom: 30px;
    }

    > button {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 9px 20px;
      margin-top: 12px;
      height: 42px;
      background: var(--background-color);
      color: var(--text-color);
      font-size: 1rem;
      border: 2px solid var(--gray-color-12);

      &.start-quiz-button {
        font-weight: 700;
        color: var(--primary-color);
        border: 2px solid var(--primary-color);
      }

      &:hover {
        color: var(--text-white);
        background: var(--primary-color);
        border: 2px solid var(--primary-color);
      }
    }
  }

  .history-section {
    padding: 24px 12px;

    @media (min-width: 800px) {
      padding-bottom: 48px;
    }

    h2 {
      color: var(--text-color);
      margin-bottom: 20px;
    }

    .history-item {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      padding: 12px;
      margin: 10px 0;
      background: var(--background-color);
      border: 3px solid var(--gray-color-13);
      transition: all 0.3s ease;

      @media (min-width: 800px) {
        align-items: center;
      }

      &:hover {
        border: 3px solid var(--gray-color-12);
      }

      .history-info {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        position: relative;
        width: 100%;

        @media (min-width: 800px) {
          align-items: center;
          flex-direction: row;
          justify-content: space-between;
        }

        h3 {
          margin-bottom: 6px;

          @media (min-width: 800px) {
            margin-bottom: 0;
          }
        }

        p {
          font-size: 0.875rem;
          color: var(--gray-color-10);

          &.correct-rate {
            position: absolute;
            top: 0;
            right: 0;
            font-size: 2rem;
            font-weight: 700;
            color: var(--primary-color);
            font-style: italic;

            @media (min-width: 800px) {
              position: relative;
            }
          }
        }
      }

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 9px 12px;
        margin-left: 24px;
        min-width: fit-content;
        font-size: 0.875rem;
        background-color: var(--background-color);
        border: 2px solid var(--gray-color-12);

        &:hover {
          background: var(--gray-color-13);
        }
      }
    }
  }

  .square-1,
  .square-2,
  .square-3,
  .square-4 {
    z-index: -1;
    position: fixed;
    width: 720px;
    height: 720px;
    border: 12px solid var(--gray-color-13);
    //opacity: 0.6;
    transition: 8s cubic-bezier(0.4, 0, 0.2, 1);
    transform: translate(50%, 50%);
    -webkit-transform: translate(50%, 50%);
    -moz-transform: translate(50%, 50%);
    -ms-transform: translate(50%, 50%);
    -o-transform: translate(50%, 50%);
    -webkit-transition: 8s cubic-bezier(0.4, 0, 0.2, 1);
    -moz-transition: 8s cubic-bezier(0.4, 0, 0.2, 1);
    -ms-transition: 8s cubic-bezier(0.4, 0, 0.2, 1);
    -o-transition: 8s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .square-1 {
    top: 20%;
    left: -20%;
  }

  .square-2 {
    top: -20%;
    left: 50%;
  }

  .square-3 {
    top: -50%;
    left: 20%;
  }

  .square-4 {
    top: 50%;
    left: 0%;
  }
}
